@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
}
body{
  text-align:center;
  background-color: #1b2034;
  color: #Fff;
}
.App{
  padding:2rem 1rem ;
}
h1{
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
input{
  appearance: none;
  outline: none;
  border: none;
  width: 100%;
  max-width: 300px;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  margin-right: 1rem;
}
button,a{
  appearance: none;
  outline:none;
   border: none;
   background: none;
   color: #2bcb4b;
   font-size: 1.5rem;
   text-decoration: none;
   cursor: pointer;
}
img{
  display: block;
  width: 100%;
  max-width: 400px;
  margin: 2rem auto;
}